export const verdict = (balls: number): { smile: string, result: string } => {
  if (balls <= 20) {
    return {
      smile: '',
      result: '        .       ,       .       ,             .     ,   ...',
    };
  }

  if (balls > 20 && balls <= 40) {
    return {
      smile: '',
      result: ' ,    .   ().  ,     )',
    };
  }

  if (balls > 40 && balls <= 60) {
    return {
      smile: '',
      result: ' ,  .  ,       .        2-3 ,   "",        .',
    };
  }

  if (balls > 60 && balls <= 80) {
    return {
      smile: '',
      result: '   . -    ,  - .   !      ,         IT.',
    };
  }

  if (balls > 80 && balls <= 1000) {
    return {
      smile: '',
      result: '   !    ?',
    };
  }

  return {
    smile: '',
    result: '!  ',
  };
};